.node {
  border: 2px solid black;
  position: absolute;
  background: transparent url("") no-repeat 50%/cover;
  overflow: hidden;
  opacity: 0.8;
  -webkit-transition: opacity 0.8s;
  transition: opacity 0.8s;
  cursor: pointer; }
  .node .label {
    display: inline;
    font-family: sans-serif;
    text-transform: uppercase;
    font-size: 1rem;
    color: rgba(255, 255, 255, 0.6);
    position: absolute;
    padding: 0;
    margin: 0;
    bottom: 10%;
    left: 10%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    -webkit-transition: color 0.4s, opacity 0.8s, -webkit-filter 0.8s;
    transition: color 0.4s, opacity 0.8s, -webkit-filter 0.8s;
    transition: color 0.4s, opacity 0.8s, filter 0.8s;
    transition: color 0.4s, opacity 0.8s, filter 0.8s, -webkit-filter 0.8s; }
  .node.hide {
    opacity: 0;
    pointer-events: none; }
    .node.hide .label {
      -webkit-filter: blur(10px);
              filter: blur(10px); }
  .node:hover .label {
    color: white; }
  .node.level-0 {
    z-index: 4;
    font-size: 15vmin;
    display: none; }
  .node.level-1 {
    z-index: 3;
    font-size: 10vmin; }
  .node.level-2 {
    z-index: 2;
    font-size: 5vmin; }
  .node.level-3 {
    z-index: 1;
    font-size: 2.5vmin; }

